
.section2{
  font-family: "Roboto";
  div{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-family: "roboto";
    flex-wrap: wrap;
    div{
      flex-direction: column;
    }
      div{
        h3{
          font-size: 2rem;
          margin-top: 2rem;
          color:$grey_fonts;
          span{
            font-size:1.5rem;
            font-weight: normal;
            margin-left:4rem;
          }
        }
        h3::after{
          content:"";
          width: 12.2rem;
          height: 0.8rem;
          background-color: $red; ;
          display: block;
        }
        .Rectangle{
          background-color: $red;
          padding:2rem;
          border-radius:1.5rem;
          color:white;
          margin-top: 2rem;
          &:hover{
            cursor:pointer;
        }
      }
      .cpt {
        display: flex;
        flex-direction: row;
        p {
          font-size: 2rem;
          margin-left: 9rem;
          margin-right: 2rem;
        }
        .CompteArebour {
          display: flex;
          flex-direction: row;
          #contour{
            border: 1px solid grey;
            border-radius:1rem;
            padding:1.5rem;
            font-size:1.5rem;
          }
        }
        span{
          font-size: 3rem;
          margin-right:1rem;
          margin-left:1rem;
        }
      }
    }
  }
}

@media screen and (max-width : 800px) {
  .section2 {
    h3{
      padding:1rem;
    }
    h3 span{
      display: inline-block;
      margin-left: 0!important;
      font-size:1.3rem!important;
    }
    .cpt span{
      margin:0!important;
    }
    .cpt p{
      margin:0!important;
      font-size:1.3rem!important;
      padding-bottom:1rem;
      display: inline-block;
    }
    .CompteArebour{
      display: inline;
    }
    .cpt{
      flex-direction: column!important;
    }



  }
}